/* ==========================================================================
   Header main menu style - begin
   ========================================================================== */

.mkdf-main-menu {
    @include mkdfRelativeHolderLayout();
    height: 100%;
    width: auto;

    ul {
        list-style: none outside none;
        margin: 0;
        padding: 0;
        text-align: left;

        li {
            position: relative;
            display: inline-block;
            float: left;
            padding: 0;
            margin: 0;

            a {
                font-size: 16px;
                line-height: 20px;
                color: #8a959e;

                &:hover {
                    color: $first-main-color;
                }

                i:before {
                    margin: 0 7px 0 0;
                }

                .menu_icon_wrapper {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                }

                .mkdf-menu-featured-icon {
                    position: relative;
                    top: -4px;
                    left: 5px;
                    color: currentColor;
                    font-size: 10px;
                    margin: 0;

                    &:before {
                        margin: 0;
                    }
                }
            }
        }
    }

    > ul {
        @include mkdfRelativeHolderLayout();
        height: 100%;

        > li {
            float: left;
            height: 100%;
            box-sizing: border-box;

            > a {
                @include mkdfRelativeHolderLayout();
                height: 100%;
                padding: 0 23px;
                color: $default-heading-color;
                font-size: 16px;
                line-height: 22px;
                letter-spacing: -0.04em;
                box-sizing: border-box;
                font-weight: 500;

                > span.item_outer {
                    position: relative;
                    display: block;
                    top: 50%;
                    @include mkdfTransform(translateY(-50%));

                    .item_text {
                        position: relative;
                        display: inline-block;
                        vertical-align: top;
                    }
                }
            }

            &.mkdf-active-item > a {
                color: $first-main-color;
            }

            &.has_sub {

                > a .mkdf-menu-arrow {
                    display: none;
                }
            }
        }
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header) {

    .mkdf-main-menu > ul > li {

        > a {
            color: $header-light-color;

            &:hover {
                color: $header-light-hover-color;
            }
        }

        &.mkdf-active-item > a {
            color: $header-light-hover-color;
        }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header) {

    .mkdf-main-menu > ul > li {

        > a {
            color: $header-dark-color;

            &:hover {
                color: $first-main-color;
            }
        }

        &.mkdf-active-item > a {
            color: $first-main-color;
        }
    }
}

/* Narrow drop down styles
   ========================================================================== */
.mkdf-drop-down {

    .second {
        position: absolute;
        display: block;
        left: 0;
        top: 100%;
        visibility: hidden;
        overflow: hidden;
        opacity: 0;
        z-index: 10;

        &.mkdf-drop-down-start {
            visibility: visible;
            overflow: visible;
            opacity: 1;
            @include mkdfTransition(opacity 0.2s ease-out);
        }

        &.right {
            right: 0;
            left: auto;
        }
    
        .inner {
            position: relative;
            display: block;
            padding: 0;
            margin: 0;
            z-index: 997;
    
            ul {
        
                li {
                    display: block;
                    float: none;
                    height: auto;
            
                    .widget {
                        width: 100%;
                        margin: 0 !important;

                        .mkdf-widget-title-holder {
                            margin: 0 0 24px;
                        }
                    }
            
                    a {
                        display: block;
                        float: none;
                        margin: 0;
                        padding: 5px 18px;
                
                        .item_outer {
                            position: relative;
                            display: block;
                            overflow: hidden;
                            padding: 0;
                            box-sizing: border-box;
                            @include mkdfTransition(padding .2s ease-in-out);
                    
                            &:before {
                                content: '\25CF';
                                position: absolute;
                                top: -1px;
                                left: 0;
                                width: 20px;
                                font-family: 'Ionicons';
                                color: inherit;
                                font-size: 9px;
                                line-height: inherit;
                                font-weight: normal;
                                font-style: normal;
                                text-rendering: auto;
                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;
                            }

                            .item_text {
                                padding-left: 15px;
                            }
                        }
                    }
            
                    &.current-menu-ancestor > a,
                    &.current-menu-item > a {
                        color: $first-main-color;
                    }
            
                    &.sub {
                
                        > a {
                    
                            .item_outer {
                        
                                &:after {
                                    position: absolute;
                                    display: inline-block;
                                    vertical-align: middle;
                                    top: 0;
                                    right: 3px;
                                    content: '\f3d3';
                                    font-family: 'Ionicons';
                                    font-size: 17px;
                                    font-weight: normal;
                                    font-style: normal;
                                    text-rendering: auto;
                                    -webkit-font-smoothing: antialiased;
                                    -moz-osx-font-smoothing: grayscale;
                                }
                            }
                        }
                    }
                }
        
                &.right {
            
                    li {
                
                        a {
                    
                            .item_outer {
                                padding: 0 0 0 30px;
                        
                                &:before {
                                    content: '\f3d5';
                                    left: auto;
                                    right: -20px;
                                    @include mkdfTransition(right .2s ease-in-out);
                                }

                                &:before {
                                    content: '\25CF';
                                    position: absolute;
                                    top: 0;
                                    right: 0;
                                    width: 20px;
                                    font-family: 'Ionicons';
                                    color: inherit;
                                    font-size: 9px;
                                    line-height: inherit;
                                    font-weight: normal;
                                    font-style: normal;
                                    text-rendering: auto;
                                    -webkit-font-smoothing: antialiased;
                                    -moz-osx-font-smoothing: grayscale;
                                }

                                .item_text {
                                    padding-left: 0;
                                    padding-right: 15px;
                                }
                            }

                        }
                
                        &.current-menu-ancestor > a,
                        &.current-menu-item > a {
                    
                            .item_outer {
                                padding: 0 20px 0 10px;
                        
                                &:before {
                                    left: auto;
                                    right: 0;
                                }
                            }
                        }
                
                        &.sub {
                    
                            > a {
                        
                                .item_outer {

                                    &:after {
                                        right: auto;
                                        left: 3px;
                                        content: "\f3d2";
                                    }
                                }
                            }
                        }
                
                        a {
                            text-align: right;
                        }
                
                        ul {
                            left: -100%;
                        }
                    }
                }
            }
        }
    }

    > ul > li {
        &:hover > .second {
            z-index: 20;
            box-shadow:0 1px 23.83px 0.17px rgba(255,51,102,.07);
        }
    }

    .narrow .second .inner {

        ul {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            background-color: #fff;
            padding: 16px 0;

            li {
                width: 220px;

                ul {
                    position: absolute;
                    left: 100%;
                    top: -15px;
                    height: auto;
                    opacity: 0;
                    overflow: hidden;
                    z-index: 10;
                    box-shadow: 0 5px 5px 0 rgba(0,0,0,0.03);
                    -webkit-transform: translateZ(0);
                    @include mkdfTransition(opacity .4s ease-out);
                }

                &:hover ul {
                    z-index: 20;
                    opacity: 1;
                }
            }

            &.right li ul {
                left: -100%;
            }
        }
    }
}

/* Wide drop down styles
   ========================================================================== */
.mkdf-drop-down .wide {

	&.mkdf-menu-2-columns {

		.second {

			.inner {

				> ul {

                    width: 610px !important;
                    padding: 32px 0 36px;

					> li {
						width: 60%;

						&:first-child {
							width: 40%;
						}

						@include laptop-landscape-medium {
							width: 60%;

							&:first-child {
								width: 40%;
							}
						}

                        > a {
                            padding: 2px 0 13px;
                        }
					}
				}
			}
		}
	}

    .second {
        left: auto;
        right: auto;

        .inner {
            background-color: #fff;
            text-align: center;

            ul {

                li {

                    a {
                        padding: 5px 13px;

                        &.mkdf-ls-item-author-image {
                            padding: 0;
                        }
                    }

                    ul {
                        position: relative;
                        display: block;
                        top: 0;
                        left: 0;
                        visibility: visible;

                        li {

                            &:last-child {

                                a {
                                    padding-bottom: 0;
                                }
                            }
                        }
                    }
                }
            }

            > ul {
                display: flex;
                flex-wrap: wrap;
                padding: 32px 0 31px 0;
                box-sizing: border-box;
                width: 900px;

                @media only screen and (min-width: 1400px) {
                    .mkdf-grid-1300 & {
                        width: $grid-width-1300;
                    }
                }

                @media only screen and (min-width: 1300px) {
                    .mkdf-grid-1200 & {
                        width: $grid-width-1200;
                    }
                }

                @media only screen and (min-width: 1200px) {
                    .mkdf-grid-1000 & {
                        width: $grid-width-1000;
                    }
                }

                @media only screen and (min-width: 1024px) {
                    .mkdf-grid-800 & {
                        width: $grid-width-800;
                    }
                }

                > li {
                    width: 225px;
                    margin: 0;
                    padding: 0 15px;
                    box-sizing: border-box;

                    &:nth-child(4n+1) {
                        clear: both;
                    }

                    > a {
                        padding: 0 0 13px;
                        color: $default-heading-color;
                        font-size: 19px;
                        line-height: 1.1em;
                        font-weight: 500;

                        &.no_link {
                            cursor: default;
                        }

                        .item_outer {
                            padding: 0;
                            overflow: inherit;

                            &:before,
                            &:after {
                                display: none;
                            }
                        }
                    }

                    &.current-menu-ancestor > a,
                    &.current-menu-item > a {
                        color: $default-heading-color;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   Header main menu style - end
   ========================================================================== */