/* ==========================================================================
   Top header style - begin
   ========================================================================== */

.mkdf-top-bar {
	@include mkdfRelativeHolderLayout();
	height: 40px;
	color: #fff;
	font-size: 11px;
	line-height: 1;
	background-color: #252b33;
	z-index: 111;
	
	> .mkdf-vertical-align-containers {
		padding: 0 60px;
	}
	
	.mkdf-grid {
		height: 100%;
		
		.mkdf-vertical-align-containers {
			padding-left: 0;
			padding-right: 0;
			
			.mkdf-top-bar-in-grid-padding & {
				padding: 0 60px;
			}
		}
	}

	.mkdf-search-opener {
		font-size: 15px;
	}
	
	.widget {
		margin: 0;
		background-color: transparent;
		border: 0;

		a {
			color: $header-light-color;
			font-size: 13px;
			letter-spacing: -0.02em;


			&:hover {
				color: $first-main-color;
			}
		}
	}

	.mkdf-icon-widget-holder {
		.mkdf-icon-element {
			margin: 2px 0 0;
		}
	}
}

.mkdf-dark-header {
	.mkdf-top-bar {
		a {
			color: $header-dark-color;

			&:hover {
				color: $first-main-color;
			}
		}

		.widget {

			a {
				color: $header-dark-color;

				&:hover {
					color: $first-main-color;
				}
			}
		}
	}
}

// Header Top Light Skin
.mkdf-top-light-header {

	.mkdf-top-bar {

		.mkdf-vertical-align-containers {

			.widget_icl_lang_sel_widget #lang_sel .lang_sel_sel:after,
			.widget_icl_lang_sel_widget #lang_sel_click .lang_sel_sel:after {
				color: $header-light-color;
			}

			.widget_icl_lang_sel_widget #lang_sel ul ul,
			.widget_icl_lang_sel_widget #lang_sel_click ul ul {
				background-color: $header-dark-color;
			}

			.widget_icl_lang_sel_widget #lang_sel_click ul li a {
				color: $header-light-color;
			}

			a {
				color: $header-light-color !important;

				&:hover {
					color: $first-main-color !important;
				}
			}

			.widget ul li a {
				color: $header-light-color;

				&:hover {
					color: $first-main-color;
				}
			}
		}
	}
}

// Header Top Dark Skin
.mkdf-top-dark-header {

	.mkdf-top-bar {

		.mkdf-vertical-align-containers {

			.widget_icl_lang_sel_widget #lang_sel .lang_sel_sel:after,
			.widget_icl_lang_sel_widget #lang_sel_click .lang_sel_sel:after {
				color: $header-dark-color;
			}

			.widget_icl_lang_sel_widget #lang_sel ul ul,
			.widget_icl_lang_sel_widget #lang_sel_click ul ul {
				background-color: $header-light-color;
			}

			.widget_icl_lang_sel_widget #lang_sel_click ul li a {
				color: $header-dark-color;
			}

			a {
				color: $header-dark-color !important;

				&:hover {
					color: $first-main-color !important;
				}
			}

			.widget ul li a {
				color: $header-dark-color;

				&:hover {
					color: $first-main-color;
				}
			}
		}
	}
}

/* ==========================================================================
   Top header style - end
   ========================================================================== */