/* ==========================================================================
   Fixed header style - begin
   ========================================================================== */

.mkdf-page-header {
    
    .mkdf-fixed-wrapper {
        
        &.fixed {
            position: fixed;
	        top: 0;
	        left: 0;
	        width: 100%;
            -webkit-backface-visibility: hidden;
            
            .admin-bar & {
                margin-top: 32px;
            }
	
	        .mkdf-menu-area {
		        background-color: #252b33;
				border-bottom: 1px solid #252b33;
	        }
        }
	
	    .mkdf-fixed-on-scroll & {
		
		    .mkdf-menu-area {
			    @include mkdfTransition(height .2s ease);
		    }
	    }
    }
}

.mkdf-fixed-wrapper {
	
	&.fixed {
		
		.mkdf-main-menu {
			
			> ul {
				
				> li {
					
					&.mkdf-active-item {
					
						> a {
							color: $header-light-hover-color;
						}
					}
					
					> a {
						color: $header-light-color;
						
						&:hover {
							color: $header-light-hover-color;
						}
					}
				}
			}
		}
	}
}

/* ==========================================================================
   Fixed header style - end
   ========================================================================== */