/* ==========================================================================
   Mobile header style - begin
   ========================================================================== */

.mkdf-mobile-header {
    position: relative;
    display: none;
    z-index: 110;

    /* #General mobile header styles
    ========================================================================== */
    .mkdf-mobile-header-inner {
        position: relative;
        height: 70px;
        background-color: #fff;
        border-bottom: 1px solid $default-border-color;
        box-sizing: border-box;

        .mkdf-mobile-header-holder {
            position: relative;
            z-index: 2;
            height: 100%;
        }
    }

    .mkdf-grid {
        height: 100%;
    }

    .mkdf-vertical-align-containers {
        padding: 0;
    }

    /* #End of general mobile header styles
   ========================================================================== */

    /* #Mobile logo styles
    ========================================================================== */
    .mkdf-mobile-logo-wrapper {

        a {
            display: block;
        }

        img {
            display: block;
            height: 100%;
            width: auto;
            padding-left: 10px;
        }
    }
    /* #End of mobile logo styles
    ========================================================================== */

    /* #Mobile menu opener styles
    ========================================================================== */
    .mkdf-mobile-menu-opener {
        position: relative;
        height: 100%;
        float: left;
        z-index: 2;

        a {
            display: table;
            height: 100%;
            padding-right: 10px;

            > * {
                position: relative;
                display: table-cell;
                vertical-align: middle;
                line-height: 1em;
            }

            .mkdf-mobile-menu-icon {
                font-size: 16px;

                i {
                    margin-left: -4px;
                }
            }

            .mkdf-mobile-menu-text {
                @include mkdfTypographyLayout();
                padding-left: 5px;
            }
        }

        &.mkdf-mobile-menu-opened {

            a {
                color: $first-main-color;
            }
        }
    }
    /* #End of mobile menu opener styles
    ========================================================================== */

    /* #Mobile menu navigation styles
    ========================================================================== */
    .mkdf-mobile-side-area {
        background-color: #fff;
        height: 100%;
        min-height: 100%;
        overflow: hidden;
        padding: 56px 51px 80px 51px;
        box-sizing: border-box;
        position: fixed;
        top: 0;
        right: 0px;
        width: 400px;
        z-index: 9999;
        -webkit-backface-visibility: hidden;
        @include mkdfTransform(translate3d(400px, 0, 0));
        @include mkdfTransition(transform .7s cubic-bezier(0.25, 1, .2, 1));

        &.opened {
            @include mkdfTransform(translate3d(0, 0, 0));
            box-shadow: 0 1px 23.28px 0.72px rgba(255, 51, 102, 0.07);


            .mkdf-mobile-nav,
            .mkdf-mobile-menu-bottom {
                @include mkdfTransform(translateX(0px));
                opacity: 1;
            }
        }

        .mkdf-mobile-side-area-inner {
            height: 100%;
            min-height: 100%;
            overflow: hidden;

        }

        .mkdf-mobile-widget-area {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            display: block;
            height: 60px;
            box-sizing: border-box;

            > * {
                box-sizing: border-box;
                float: left;
                height: 60px;
                margin: 0;
                text-align: center;
                line-height: 60px;
                display: inline-block;
                vertical-align: middle;
                color: $additional-text-color;
                font-size: 24px;

            }

            .mkdf-mobile-widget-area-inner {
                position: relative;
                width: 100%;

                > div {
                    display: inline-block;
                }
            }
        }

        .mkdf-close-mobile-side-area-holder {
            display: block;
            position: absolute;
            top: 19px;
            right: 21px;
            z-index: 1000;
            cursor: pointer;

            i {
                color: $first-main-color;
                font-size: 22px;
            }
        }

        .mkdf-mobile-nav {
            opacity: 0;
            @include mkdfTransform(translateX(50px));
            @include mkdfTransition(transform .45s cubic-bezier(.2, .75, .3, 1) 0.22s, opacity .45s cubic-bezier(.2, .75, .3, 1) 0.22s);

            ul {
                list-style-type: none;
                padding: 0;
                margin: 0;

                li {
                    position: relative;
                    font-size: 19px;
                    color: $default-heading-color;
                    line-height: 1.26em;
                    text-transform: none;
                    font-weight: 500;

                    ul {
                        display: none;
                        margin: 3px 0 0px;
                    }

                    a, h4 {
                        //h4 is here because of no link option in navigation
                        font-size: inherit;
                        color: inherit;
                        line-height: inherit;
                        padding: 14px 0;
                        margin: 0;
                        position: relative;
                        text-transform: inherit;
                        letter-spacing: 0;
                        display: inline-block;
                        vertical-align: middle;
                    }

                    .mobile_arrow {
                        font-size: 13px;
                        margin-left: 15px;
                        color: $default-heading-color;
                        display: inline-block;
                        vertical-align: middle;

                        &:hover {
                            cursor: pointer;
                        }
                    }

                    .item_additional_label {
                        font-size: 9px;
                        margin-left: 5px;
                        color: #ff4b4b;
                        vertical-align: middle;
                        display: inline-block;
                        font-weight: 500;
                    }

                    ul li {
                        color: $additional-text-color;

                        &.mkdf-extended-item-font-weight {
                            color: #292929;
                        }

                        a, h4 {
                            font-size: 16px;
                            color: inherit;
                            line-height: 1.26em;
                            font-weight: 400;
                            padding: 8px 0 8px 15px;
                        }

                        a {
                            &:before {
                                content: '';
                                position: absolute;
                                left: 0;
                                top: 50%;
                                @include mkdfTransform(translateY(-50%));
                                width: 4px;
                                height: 4px;
                                background-color: $additional-text-color;
                            }
                            &:hover {
                                &:before{
                                    background-color: $first-main-color;
                                }
                            }
                        }

                        &.mkdf-opened, &.mkdf-active-item, &.current_page_item, &:hover {
                            color: $first-main-color;
                        }
                        &.mkdf-opened,
                        &.current_page_item,
                        &.mkdf-active-item,
                        &:hover {
                            > a:before {
                                background-color: $first-main-color;
                            }

                        }
                    }

                    &.current-menu-parent {
                        ul {
                            display: block;
                        }
                    }
                }
            }

            > ul > li > ul > li > ul {

                &:after, &:before {
                    content: " ";
                    display: table;
                }

                &:after {
                    clear: both;
                }

                > li {
                    padding-left: 17px;
                    float: left;
                    font-size: 14px;
                    box-sizing: border-box;
                    //color: #000 !important; //!important rule to override additional classes styles

                    &:nth-child(2n + 1) {
                        clear: both;
                    }

                    .mobile_arrow {
                        display: none;
                    }

                    ul {
                        display: block !important; //!important rule to override JS inline style
                        margin: 2px 0;
                    }

                }
            }

            //> ul > li {
            //
            //    > a:after {
            //        content: '';
            //        height: 1px;
            //        width: 0;
            //        background-color: #000;
            //        position: absolute;
            //        top: calc(100% - 10px);
            //        left: 0;
            //
            //        @include mkdfTransition(width 0.3s ease);
            //    }
            //
            //    &.mkdf-opened, &.mkdf-active-item, &:hover {
            //        > a:after {
            //            width: 100%;
            //        }
            //    }
            //}

            & > ul > li:last-child > a,
            & > .mkdf-grid > ul > li:last-child > a {
                border-bottom: none;
            }
        }
    }

    /* #End of mobile menu navigation styles
    ========================================================================== */
}

/* ==========================================================================
   Mobile header style - end
   ========================================================================== */